#container22 { margin: 0%; position: absolute; top:0px; left: 0px; width: 100%;}

#circle22 { position: relative; width: 100%; padding-bottom: 100%; overflow: hidden; }

#circle22 text { font-size: 32px; font-weight: bold; color:white; }

#circle22 svg { position: absolute; left: 0; top: 0; 
  width: 100%; 
  height: 100%;

  -webkit-animation-name: rotate22;
     -moz-animation-name: rotate22;
      -ms-animation-name: rotate22;
       -o-animation-name: rotate22;
          animation-name: rotate22;
  -webkit-animation-duration: 5s;
     -moz-animation-duration: 5s;
      -ms-animation-duration: 5s;
       -o-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
     -moz-animation-iteration-count: infinite;
      -ms-animation-iteration-count: infinite;
       -o-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
     -moz-animation-timing-function: linear;
      -ms-animation-timing-function: linear;
       -o-animation-timing-function: linear;
          animation-timing-function: linear;

}

@-webkit-keyframes rotate22 {
    from { -webkit-transform: rotate(360deg); }
    to { -webkit-transform: rotate(0); }
}
@-moz-keyframes rotate22 {
    from { -moz-transform: rotate(360deg); }
    to { -moz-transform: rotate(0); }
}
@-ms-keyframes rotate22 {
    from { -ms-transform: rotate(360deg); }
    to { -ms-transform: rotate(0); }
}
@-o-keyframes rotate22 {
    from { -o-transform: rotate(360deg); }
    to { -o-transform: rotate(0); }
}
@keyframes rotate22 {
    from { transform: rotate(360deg); }
    to { transform: rotate(0); }
}


